<script lang="ts" module>import {
  scrollSpeed as __scrollSpeed,
  scrollStop as _scrollStop,
  getListIndices
} from "../utils";
const scrollStop = _scrollStop();
const _scrollSpeed = __scrollSpeed();
</script>

<script lang="ts">let {
  itemCount,
  itemSize,
  height = "100%",
  width = "100%",
  stickyIndices = [],
  overScan = 1,
  marginLeft = 0,
  marginTop = 0,
  layout = "vertical",
  scrollPosition = $bindable(0),
  scrollAlignment = "auto",
  scrollBehavior = "auto",
  getKey = (index) => index,
  onscroll,
  header,
  item,
  placeholder,
  footer,
  ...rest
} = $props();
let list = $state();
let _scrollPosition = $state(scrollPosition);
let headerHeight = $state(0);
let headerWidth = $state(0);
let offsetHeight = $state(0);
let clientHeight = $state(0);
let offsetWidth = $state(0);
let clientWidth = $state(0);
let indices = $state([]);
let manualScroll = false;
let isScrolling = false;
let isScrollingFast = $state(false);
export function scrollToIndex(index, alignment = scrollAlignment, behavior = scrollBehavior) {
  scrollTo(getScrollToPosition(index, alignment), behavior);
}
export function scrollToPosition(position, behavior = scrollBehavior) {
  scrollTo(position, behavior);
}
const getScrollToPosition = (index, alignment) => {
  const extra = isVertical ? marginTop + headerHeight : marginLeft + headerWidth;
  const maxOffset = index * itemSize + extra;
  const minOffset = maxOffset - size + itemSize + extra;
  let offset;
  switch (alignment) {
    case "start":
      offset = maxOffset;
      break;
    case "center":
      offset = maxOffset - (size - itemSize) / 2;
      break;
    case "end":
      offset = minOffset;
      break;
    default:
      offset = Math.max(minOffset, Math.min(maxOffset, scrollPosition));
      break;
  }
  return Math.max(0, Math.min(innerSize - size, offset));
};
const scrollTo = (direction, behavior = scrollBehavior) => {
  if (list) {
    manualScroll = true;
    list.scrollTo({ [isVertical ? "top" : "left"]: direction, behavior });
    manualScroll = false;
  }
};
const scrollToManual = (scrollPosition2) => {
  if (list && !manualScroll && !isScrolling) {
    manualScroll = true;
    list.scrollTo({ top: scrollPosition2, behavior: scrollBehavior });
    manualScroll = false;
  }
};
const getItemStyle = (index) => {
  const ixis = index * itemSize;
  return `position: absolute; transform: translate3d(${isVertical ? `${marginLeft}px, ${ixis + marginTop}px` : `${headerWidth + ixis + marginLeft}px, ${marginTop}px`}, 0px); ${itemSizeInternal} will-change: transform;`;
};
const onScroll = (event) => {
  isScrolling = true;
  if (!manualScroll) {
    if (isVertical) {
      _scrollPosition = Math.max(0, event.currentTarget["scrollTop"] - headerHeight);
      scrollPosition = event.currentTarget["scrollTop"];
    } else {
      _scrollPosition = Math.max(0, event.currentTarget["scrollLeft"] - headerWidth);
      scrollPosition = event.currentTarget["scrollLeft"];
    }
    scrollSpeed(_scrollPosition);
  }
  scrollStop(() => {
    isScrolling = false;
  });
};
let isVertical = $derived(layout === "vertical");
let innerSize = $derived(itemCount * itemSize);
let itemSizeInternal = $derived(
  isVertical ? `height: ${itemSize}px; width: ${marginLeft > 0 ? `${clientWidth - marginLeft}px` : "100%"};` : `height: ${marginTop > 0 ? `${clientHeight - marginTop}px` : "100%"}; width: ${itemSize}px;`
);
let size = $derived(isVertical ? offsetHeight : offsetWidth);
$effect(() => {
  if (size) {
    indices = getListIndices(itemCount, itemSize, size, overScan, _scrollPosition);
  }
});
$effect(() => {
  if (list) {
    scrollToManual(scrollPosition);
  }
});
let scrollSpeed = $derived(
  _scrollSpeed(size, {
    fast: () => {
      isScrollingFast = true;
    },
    slow: () => {
      isScrollingFast = false;
    }
  })
);
</script>

<!--
@component

- Usage:

```svelte
	<script>
		import { List } from "svelte-virtual";

		let items = [...];
	</script>

	<List itemCount={items.length} itemSize={20} height={500}>
		{#snippet item({ index, style })}
			<div {style}>
				{items[index]}
			</div>
		{/snippet}
	</List>
```
-->
<div
	style:position="relative"
	style:overflow="auto"
	style:height={typeof height === "number" ? `${height}px` : height}
	style:width={typeof width !== "number" ? width : `${width}px`}
	onscroll={(e) => {
		onScroll(e);

		onscroll?.(e);
	}}
	bind:this={list}
	bind:offsetHeight
	bind:clientHeight
	bind:offsetWidth
	bind:clientWidth
	{...rest}
>
	{#if header}
		{#if isVertical}
			<div bind:offsetHeight={headerHeight}>
				{@render header()}
			</div>
		{:else}
			<div bind:offsetWidth={headerWidth} style:position="absolute">
				{@render header()}
			</div>
		{/if}
	{/if}

	<div
		style:height={isVertical ? `${innerSize}px` : "100%"}
		style:width={!isVertical ? `${innerSize}px` : "100%"}
	>
		{#if stickyIndices.length && indices.length}
			{@const stickyIndex = Math.max(...stickyIndices.filter((i) => i < indices[0]))}

			{#if stickyIndex >= 0}
				<div
					style:position="sticky"
					style:top={isVertical ? `${marginTop}px` : "0px"}
					style:left={isVertical ? "0px" : `${marginLeft}px`}
					style:z-index="1"
				>
					{@render item({ index: stickyIndex, style: "" })}
				</div>
			{/if}
		{/if}

		{#each indices as index (getKey(index))}
			{@const style = getItemStyle(index)}

			{#if !isScrollingFast || !placeholder}
				{@render item({ index, style })}
			{:else}
				{@render placeholder({ index, style })}
			{/if}
		{/each}
	</div>

	{#if footer}
		{#if isVertical}
			<div>
				{@render footer()}
			</div>
		{:else}
			<div
				style:position="absolute"
				style:top="0px"
				style:left="{headerWidth + itemCount * itemSize + marginLeft}px"
			>
				{@render footer()}
			</div>
		{/if}
	{/if}
</div>
