<script lang="ts" module>import {
  scrollSpeed as __scrollSpeed,
  scrollStop as _scrollStop,
  getGridIndices,
  getRowIndex,
  round
} from "../utils";
const scrollStop = _scrollStop();
const _scrollSpeed = __scrollSpeed();
</script>

<script lang="ts">let {
  itemCount,
  itemHeight,
  itemWidth,
  height,
  width = "100%",
  overScan = 1,
  marginLeft = 0,
  marginTop = 0,
  scrollPosition = $bindable(0),
  scrollBehavior = "auto",
  getKey = (index) => index,
  columnCount,
  onscroll,
  header,
  item,
  placeholder,
  footer,
  ...rest
} = $props();
let grid = $state();
let _scrollPosition = $state(scrollPosition);
let headerHeight = $state(0);
let offsetWidth = $state(0);
let clientWidth = $state(0);
let indices = $state([]);
let manualScroll = false;
let isScrolling = false;
let isScrollingFast = $state(false);
export function scrollToIndex(index, behavior = scrollBehavior) {
  scrollTo(
    getRowIndex(index, _columnCount) * itemHeight + marginTop + headerHeight,
    behavior
  );
}
export function scrollToPosition(position, behavior = scrollBehavior) {
  scrollTo(position, behavior);
}
const scrollTo = (top, behavior = scrollBehavior) => {
  if (grid) {
    manualScroll = true;
    grid.scrollTo({ top, behavior });
    manualScroll = false;
  }
};
const scrollToManual = (scrollPosition2) => {
  if (grid && !manualScroll && !isScrolling) {
    manualScroll = true;
    grid.scrollTo({ top: scrollPosition2, behavior: scrollBehavior });
    manualScroll = false;
  }
};
const getItemProps = (index) => {
  const rowIndex = getRowIndex(index, _columnCount);
  const columnIndex = index % _columnCount;
  return {
    rowIndex,
    columnIndex,
    style: `position: absolute; transform: translate3d(${columnIndex * itemWidth + marginLeft}px, ${rowIndex * itemHeight + marginTop}px, 0px); height: ${itemHeight}px; width: ${itemWidth}px; will-change: transform;`
  };
};
const onScroll = (event) => {
  isScrolling = true;
  if (!manualScroll) {
    _scrollPosition = Math.max(0, event.currentTarget.scrollTop - headerHeight);
    scrollPosition = event.currentTarget.scrollTop;
    scrollSpeed(_scrollPosition);
  }
  scrollStop(() => {
    isScrolling = false;
  });
};
let _columnCount = $derived(
  !columnCount ? Math.max(~~((offsetWidth - marginLeft - (offsetWidth - clientWidth)) / itemWidth), 1) : columnCount
);
let innerHeight = $derived(round.ceil(itemCount, _columnCount) * itemHeight / _columnCount);
let overScanColumn = $derived(_columnCount * overScan);
$effect(() => {
  if (offsetWidth || _columnCount) {
    indices = getGridIndices(
      itemCount,
      itemHeight,
      height,
      _columnCount,
      overScanColumn,
      _scrollPosition
    );
  }
});
$effect(() => {
  if (grid) {
    scrollToManual(scrollPosition);
  }
});
let scrollSpeed = $derived(
  _scrollSpeed(height, {
    fast: () => {
      isScrollingFast = true;
    },
    slow: () => {
      isScrollingFast = false;
    }
  })
);
</script>

<!--
@component

- Usage:

```svelte
	<script>
		import { Grid } from "svelte-virtual";

		let items = [...];
	</script>

	<Grid itemCount={items.length} itemHeight={50} itemWidth={60} height={500}>
		{#snippet item({ index, style })}
			<div {style}>
				{items[index]}
			</div>
		{/snippet}
	</Grid>
```
-->
<div
	style:position="relative"
	style:overflow="auto"
	style:height="{height}px"
	style:width
	onscroll={(e) => {
		onScroll(e);

		onscroll?.(e);
	}}
	bind:this={grid}
	bind:offsetWidth
	bind:clientWidth
	{...rest}
>
	{#if header}
		<div bind:offsetHeight={headerHeight}>
			{@render header()}
		</div>
	{/if}

	<div style:height="{innerHeight}px" style:width="100%">
		{#each indices as index (getKey(index))}
			{@const { rowIndex, columnIndex, style } = getItemProps(index)}

			{#if !isScrollingFast || !placeholder}
				{@render item({ index, rowIndex, columnIndex, style })}
			{:else}
				{@render placeholder({ index, rowIndex, columnIndex, style })}
			{/if}
		{/each}
	</div>

	{@render footer?.()}
</div>
